  font-size: calc(40 * var(--vp));
  font-weight: 500;
}

.dashboard-certificate-complete .complete-desc {
  color: #020122;
  font: normal normal normal calc(28 * var(--vp)) Roboto;
  width: calc(549 * var(--vp));
  margin: calc(18 * var(--vp)) auto calc(27 * var(--vp));
}

.dashboard-certificate-complete
  .complete-certificate-content
  .complete-button
  button {
  font-size: calc(28 * var(--vp));
  color: #f74b42;
  text-transform: capitalize;
  width: calc(326 * var(--vp));
  height: calc(61 * var(--vp));
  background-color: transparent;
  border: 1px solid #f74b42;
  border-radius: calc(5 * var(--vp));
  margin-top: calc(23 * var(--vp));
  justify-content: space-around;
}

.dashboard-certificate-complete
  .complete-certificate-content
  .complete-button
  button:hover {
  background-color: #f74b42;
  color: #ffffff;
}
.complete-certificate-content .row .complete-button button {
  width: calc(287 * var(--vp));
}

.dashboard-certificate-complete
  .complete-certificate-content
  .complete-button
  .downloadicon {
  padding-right: calc(35 * var(--vp));
  background-image: url("../img/download.png");
