  background-size: cover;
  margin-right: 1vw;
}

.dashboard-certificate-complete
  .complete-certificate-content
  .complete-button
  button:hover
  .shareicon {
  padding-right: calc(31 * var(--vp));
  background-image: url("../img/share.png");
  background-position: center;
  background-size: cover;
  margin-right: 1vw;
}

@media (max-width: 600px) {
  .dashboard-certificate-complete {
    width: calc(326 * var(--vm));
    height: calc(495 * var(--vm));
    background-color: #ffffff;
    padding: calc(18 * var(--vm)) calc(26 * var(--vm));
    margin: calc(197 * var(--vp)) auto calc(233 * var(--vp));
    box-shadow: calc(0 * var(--vp)) calc(6 * var(--vp)) calc(6 * var(--vp))
      #00000045;
    border-radius: calc(9 * var(--vm));
  }

  .dashboard-certificate-complete .certificate-image {
    width: calc(275 * var(--vm));
    height: calc(191 * var(--vm));
    margin: auto;
  }

  .dashboard-certificate-complete .certificate-image img {
    width: 100%;
    height: 100%;
    border-radius: calc(9 * var(--vm));
  }

  .dashboard-certificate-complete .complete-certificate-content {
    text-align: center;
    margin: auto;
    padding-top: calc(20 * var(--vm));
    width: unset;
  }

  .dashboard-certificate-complete .complete-congrats {
    color: #f74b42;
    font-size: calc(20 * var(--vm));
    font-weight: 500;
  }

  .dashboard-certificate-complete .complete-desc {
    color: #020122;
    font-size: calc(13 * var(--vm));
    line-height: inherit;
    width: calc(232 * var(--vm));
    margin: calc(18 * var(--vm)) auto calc(26 * var(--vm));
  }

  .dashboard-certificate-complete
    .complete-certificate-content
    .complete-button
    button {
