    height: calc(31 * var(--vm));
    background-color: transparent;
    border: 1px solid #f74b42;
    border-radius: calc(5 * var(--vm));
    margin-bottom: calc(15 * var(--vm));
    justify-content: space-around;
  }

  .dashboard-certificate-complete
    .complete-certificate-content
    .complete-button
    button:hover {
    background-color: #f74b42;
    color: #ffffff;
  }
  .complete-certificate-content .row .complete-button button {
    width: calc(108 * var(--vm));
  }

  .complete-certificate-content .row .complete-button button .mobile-hide {
    display: none;
  }

  .complete-certificate-content .row {
    margin: 0 calc(-8 * var(--vm)) !important;
  }

  .dashboard-certificate-complete
    .complete-certificate-content
    .complete-button
    .downloadicon {
    padding-right: calc(18 * var(--vm));
    background-image: url("../img/download.png");
    background-position: center;
    background-size: contain;
    margin-right: 1vw;
  }

  .dashboard-certificate-complete
    .complete-certificate-content
    .complete-button
    .shareicon {
    padding-right: calc(14 * var(--vm));
    background-image: url("../img/shareo.png");
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    margin-right: 1vw;
  }

  .dashboard-certificate-complete
    .complete-certificate-content
    .complete-button
    button:hover
    .shareicon {
    padding-right: calc(31 * var(--vp));
    background-image: url("../img/share.png");
    background-position: center;
    background-size: cover;
    margin-right: 1vw;
  }
}
