.blogCard__horizontal {
  flex-direction: row;
  max-height: 600px;
  min-height: 550px;
}

.blogCard__horizontal .blogCard__body {
  width: 62%;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16);
  transition: all 0.3s ease-in-out;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
