.blogCard__vertical .blogCard__image {
  width: 100%;
  /* height: 40%; */
  object-fit: contain;
  border-radius: 0;
}

.blogCard__vertical .blogCard__body {
  width: 100%;
  height: 60%;
  padding: 20px;
  transition: all 0.3s ease-in-out;
}

.blogCard__vertical .blogCard__body .blogCard__title {
