  color: #190a4d;
  font-size: 24px;
  margin: 0;
}

.blogCard__info p {
  color: #a7a7aa;
  font-size: 18px;
  margin: 0;
}

.blogCard__desc {
  color: #020122;
  font-size: 18px;
  transition: all 0.3s ease-in-out;
}

